import { useContext } from 'react'
import { AppContext } from '../../App'

const UserPhoto =({photo})=>{
	const { deletePhoto } = useContext(AppContext)
	return(
		<div className="col">
			<div className="card text-center h-100">
				<div className="card-body d-flex align-items-center">
				<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-x-octagon-fill position-absolute text-danger" viewBox="0 0 16 16" onClick = {() => {if(window.confirm(`You really want to delete photo: ${photo.title}?`)){deletePhoto(photo.id)}}} style={{top: "5px", right: "5px", cursor: "pointer"}}>
  <path d="M11.46.146A.5.5 0 0 0 11.107 0H4.893a.5.5 0 0 0-.353.146L.146 4.54A.5.5 0 0 0 0 4.893v6.214a.5.5 0 0 0 .146.353l4.394 4.394a.5.5 0 0 0 .353.146h6.214a.5.5 0 0 0 .353-.146l4.394-4.394a.5.5 0 0 0 .146-.353V4.893a.5.5 0 0 0-.146-.353L11.46.146zm-6.106 4.5L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 1 1 .708-.708z"/>
</svg>
				<img src={photo.src} alt="..." className="card-img-top"/>
				</div>
				<h5 className="card-title">{photo.title}</h5>
			</div>
		</div>
	)
}

export default UserPhoto